.otaku-bread-crumbs {
  display: flex;
}
.otaku-bread-crumbs .otaku-bread-crumbs-item {
  color: #262626;
  font-size: 16px;
  cursor: pointer;
  display: flex;
  gap: 0 5px;
  padding-left: 5px;
}
.otaku-bread-crumbs .otaku-bread-crumbs-item:nth-last-of-type(1) .otaku-bread-crumbs-separator {
  display: none;
}
.otaku-bread-crumbs .otaku-bread-crumbs-item .otaku-icon-right {
  font-size: 12px;
  font-weight: bold;
}