:root {
  --radius: 4px;
  --shadow: 0 2px 8px 0 rgba(0, 35, 144, 10%);
  --disabled-color: #f7f7f7;
  --disabled-border-color: #e5e5e5;
}

.otaku-dialog-mask {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  margin: auto;
}
.otaku-dialog-mask .otaku-dialog {
  background: white;
  border-radius: var(--radius);
}
.otaku-dialog-mask .otaku-dialog-header {
  height: 60px;
  line-height: 60px;
  text-align: center;
  display: flex;
  justify-content: space-between;
  padding: 0 15px;
  border-bottom: 1px solid #e5e5e5;
  box-sizing: border-box;
}
.otaku-dialog-mask .otaku-dialog-content {
  padding: 15px;
}
.otaku-dialog-mask .otaku-dialog-footer {
  padding: 15px;
  border-top: 1px solid #e5e5e5;
}