:root {
  --radius: 4px;
  --shadow: 0 2px 8px 0 rgba(0, 35, 144, 10%);
  --disabled-color: #f7f7f7;
  --disabled-border-color: #e5e5e5;
}

.otaku-calendar-container {
  cursor: pointer;
}
.otaku-calendar-container .otaku-calendar-week {
  display: grid;
  grid-template-columns: repeat(7, 40px);
  grid-template-rows: repeat(1, 40px);
  grid-gap: 5px 10px;
  color: #262626;
  margin-bottom: 10px;
  padding: 0;
}
.otaku-calendar-container .otaku-calendar-week li {
  display: grid;
  justify-items: center;
  align-items: center;
}
.otaku-calendar-container .otaku-calendar-month {
  display: grid;
  grid-template-columns: repeat(7, 40px);
  grid-template-rows: repeat(6, 40px);
  grid-gap: 5px 10px;
}
.otaku-calendar-container .otaku-calendar-month li {
  display: grid;
  justify-items: center;
  align-items: center;
  border-radius: var(--radius);
  box-sizing: border-box;
  color: #BFBFBF;
  font-size: 12px;
}
.otaku-calendar-container .otaku-calendar-month .otaku-calendar-current {
  color: #262626;
}
.otaku-calendar-container .otaku-calendar-month .otaku-calendar-active {
  border: 1.5px solid #FF4778;
  color: #FF4778;
}
.otaku-calendar-container .otaku-calendar-month .otaku-calendar-today {
  background: #FF4778;
  color: white;
}
.otaku-calendar-container .otaku-calendar-month .otaku-calendar-disabled {
  background: #f2fcff;
  color: #595959;
  border: none;
  cursor: not-allowed;
}