:root {
  --radius: 4px;
  --shadow: 0 2px 8px 0 rgba(0, 35, 144, 10%);
  --disabled-color: #f7f7f7;
  --disabled-border-color: #e5e5e5;
}

.otaku-popover-container {
  z-index: 2000;
  cursor: pointer;
}
.otaku-popover-container .otaku-popover-tooltip {
  background: white;
  padding: 10px;
  border-radius: var(--radius);
  box-shadow: 0 2px 20px 0 rgba(0, 35, 144, 0.1);
}

.otaku-popover-arrow,
.otaku-popover-arrow::before {
  position: absolute;
  width: 12px;
  height: 12px;
  background: white;
}

.otaku-popover-arrow {
  visibility: hidden;
}

.otaku-popover-arrow::before {
  visibility: visible;
  content: "";
  transform: rotate(45deg);
}

.otaku-popover-tooltip[data-popper-placement^=top] > .otaku-popover-arrow {
  bottom: -6px;
}

.otaku-popover-tooltip[data-popper-placement^=bottom] > .otaku-popover-arrow {
  top: -6px;
}

.otaku-popover-tooltip[data-popper-placement^=left] > .otaku-popover-arrow {
  right: -6px;
}

.otaku-popover-tooltip[data-popper-placement^=right] > .otaku-popover-arrow {
  left: -6px;
}