:root {
  --radius: 4px;
  --shadow: 0 2px 8px 0 rgba(0, 35, 144, 10%);
  --disabled-color: #f7f7f7;
  --disabled-border-color: #e5e5e5;
}

.otaku-datepicker-year {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 43px 95px;
  padding-bottom: 10px;
}
.otaku-datepicker-year li {
  display: grid;
  justify-content: center;
  align-items: center;
  color: #595959;
  border-radius: var(--radius);
  cursor: pointer;
}
.otaku-datepicker-year .otaku-datepicker-currentYear {
  border: 1.5px solid #FF4778;
  color: #FF4778;
}
.otaku-datepicker-year .active {
  background: #FF4778;
  color: white;
}
.otaku-datepicker-year .gray {
  color: #BFBFBF;
}