:root {
  --radius: 4px;
  --shadow: 0 2px 8px 0 rgba(0, 35, 144, 10%);
  --disabled-color: #f7f7f7;
  --disabled-border-color: #e5e5e5;
}

.otaku-upload-container input[type=file] {
  display: none;
}
.otaku-upload-container .otaku-upload {
  width: 150px;
  height: 150px;
  border: 1px dashed #d9d9d9;
  border-radius: var(--radius);
  display: flex;
  align-items: center;
  justify-content: center;
}
.otaku-upload-container .otaku-upload .add {
  font-size: 24px;
}
.otaku-upload-container .otaku-drag-upload {
  width: 500px;
  height: 150px;
  border: 1px dashed #d9d9d9;
  border-radius: var(--radius);
  display: flex;
  align-items: center;
  justify-content: center;
}
.otaku-upload-container .otaku-drag-upload-hover {
  border: 1px dashed #FF4778;
}