:root {
  --radius: 4px;
  --shadow: 0 2px 8px 0 rgba(0, 35, 144, 10%);
  --disabled-color: #f7f7f7;
  --disabled-border-color: #e5e5e5;
}

.otaku-message {
  background: white;
  border-radius: var(--radius);
  box-shadow: var(--shadow);
  padding: 10px;
  cursor: pointer;
}
.otaku-message .otaku-message-icon {
  margin-right: 5px;
  font-size: 20px;
  vertical-align: middle;
}
.otaku-message .otaku-icon-info {
  color: #0f73f7;
}
.otaku-message .otaku-icon-success {
  color: #2DA641;
}
.otaku-message .otaku-icon-warning {
  color: #ffb540;
}
.otaku-message .otaku-icon-error {
  color: #f51010;
}