.otaku-form .otaku-form-item {
  display: grid;
  align-items: baseline;
  grid-gap: 10px;
}
.otaku-form .otaku-form-item-label {
  font-size: 14px;
}
.otaku-form .otaku-form-item-error-message {
  display: inline-block;
  color: red;
  height: 24px;
  font-size: 12px;
}
.otaku-form .otaku-form-item .is-required {
  color: red;
  margin: 2px;
}