:root {
  --radius: 4px;
  --shadow: 0 2px 8px 0 rgba(0, 35, 144, 10%);
  --disabled-color: #f7f7f7;
  --disabled-border-color: #e5e5e5;
}

.otaku-progress-box {
  display: flex;
  align-items: center;
}
.otaku-progress-box .otaku-progress-text {
  display: inline-block;
  margin-left: 5px;
}

.otaku-progress-container {
  min-width: 220px;
  height: 8px;
  background: rgba(44, 104, 255, 0.06);
  overflow: hidden;
  border-radius: 16px;
  color: white;
  text-align: right;
  box-sizing: border-box;
}
.otaku-progress-container .otaku-progress {
  height: 100%;
  transition: width 0.2s linear;
  background: #FF4778;
  box-sizing: border-box;
  font-size: 14px;
  line-height: 1;
}

.otaku-progress-vertical {
  display: flex;
  flex-direction: column-reverse;
  width: 8px;
  height: 220px;
  background: rgba(44, 104, 255, 0.06);
  overflow: hidden;
  border-radius: 16px;
  color: white;
  text-align: right;
  box-sizing: border-box;
}
.otaku-progress-vertical .otaku-progress {
  width: 100%;
  height: 0%;
  transition: height 0.2s linear;
  background: #FF4778;
  box-sizing: border-box;
  font-size: 14px;
  line-height: 1;
  writing-mode: vertical-rl;
  text-align: start;
}