:root {
  --radius: 4px;
  --shadow: 0 2px 8px 0 rgba(0, 35, 144, 10%);
  --disabled-color: #f7f7f7;
  --disabled-border-color: #e5e5e5;
}

.otaku-alert-info {
  background: #EDF4FF;
  border: 1px solid #94B4EB;
}

.otaku-alert-success {
  background: #F0FAF2;
  border: 1px solid #9FD6A8;
}

.otaku-alert-warning {
  background: #FFF5ED;
  border: 1px solid #F1924E;
}

.otaku-alert-error {
  background: #FFEBEB;
  border: 1px solid #F52F3E;
}

.otaku-alert {
  border-radius: var(--radius);
  padding: 10px;
  cursor: pointer;
}
.otaku-alert .otaku-alert-icon {
  margin-right: 5px;
  font-size: 20px;
  vertical-align: middle;
}
.otaku-alert .otaku-alert-content {
  color: #262626;
  font-size: 14px;
}
.otaku-alert .otaku-icon-info {
  color: #0f73f7;
}
.otaku-alert .otaku-icon-success {
  color: #2DA641;
}
.otaku-alert .otaku-icon-warning {
  color: #ffb540;
}
.otaku-alert .otaku-icon-error {
  color: #f51010;
}