:root {
  --radius: 4px;
  --shadow: 0 2px 8px 0 rgba(0, 35, 144, 10%);
  --disabled-color: #f7f7f7;
  --disabled-border-color: #e5e5e5;
}

.otaku-comment {
  display: flex;
}
.otaku-comment .otaku-comment-container {
  flex: 1;
  margin-left: 10px;
}
.otaku-comment .otaku-comment-container .otaku-comment-time {
  color: #999;
  font-size: 12px;
  margin-top: 5px;
}
.otaku-comment .otaku-comment-container .otaku-comment-content {
  color: #333;
  font-size: 13px;
  font-weight: 500;
  line-height: 24px;
  margin-top: 5px;
}
.otaku-comment .otaku-comment-container .otaku-comment-actions {
  display: flex;
  margin: 5px 0 10px;
  cursor: pointer;
}
.otaku-comment .otaku-comment-container .otaku-comment-actions .otaku-comment-actions-item {
  margin-right: 10px;
}
.otaku-comment .otaku-comment-container .otaku-comment-actions .otaku-comment-actions-item span {
  margin-right: 5px;
  color: #999;
}
.otaku-comment .otaku-comment-container .otaku-comment-actions .otaku-comment-actions-item .otaku-comment-like-active,
.otaku-comment .otaku-comment-container .otaku-comment-actions .otaku-comment-actions-item .otaku-comment-unlike-active,
.otaku-comment .otaku-comment-container .otaku-comment-actions .otaku-comment-actions-item .otaku-comment-reply-active {
  color: #FF6B6B;
}
.otaku-comment .otaku-comment-reply {
  color: #FF4778;
}