.otaku-table {
  width: 100%;
  border: 1px solid #e0e0e0;
  border-collapse: collapse;
}
.otaku-table th, .otaku-table tr {
  text-align: left;
}
.otaku-table th {
  display: table-row;
  width: 100%;
  background: #fafafa;
}
.otaku-table .otaku-table-multiple {
  width: 20px;
}
.otaku-table td {
  border: 1px solid #e0e0e0;
  background: white;
  padding: 6px 12px;
}