:root {
  --radius: 4px;
  --shadow: 0 2px 8px 0 rgba(0, 35, 144, 10%);
  --disabled-color: #f7f7f7;
  --disabled-border-color: #e5e5e5;
}

.otaku-datetime-picker-container {
  display: inline-block;
}

.otaku-datetime-picker {
  width: 380px;
  box-sizing: border-box;
  background: white;
  box-shadow: 0 12px 48px 0 rgba(0, 0, 0, 0.15);
  border-radius: var(--radius);
  padding: 20px 20px 0 20px;
  margin-top: 2px;
}
.otaku-datetime-picker .otaku-datetime-picker-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 40px;
  border-bottom: 1px solid #e8e8e8;
  margin-bottom: 10px;
  cursor: pointer;
}
.otaku-datetime-picker .otaku-datetime-picker-header span {
  margin-right: 5px;
}
.otaku-datetime-picker .otaku-datetime-picker-header li:nth-of-type(2) span:hover {
  color: #FF4778;
}
.otaku-datetime-picker .today {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  color: #FF4778;
  cursor: pointer;
}