:root {
  --radius: 4px;
  --shadow: 0 2px 8px 0 rgba(0, 35, 144, 10%);
  --disabled-color: #f7f7f7;
  --disabled-border-color: #e5e5e5;
}

.otaku-anchor {
  background: white;
}
.otaku-anchor-item {
  font-size: 13px;
  color: #595959;
  line-height: 28px;
  cursor: pointer;
}
.otaku-anchor-item-active {
  color: #FF4778;
}