:root {
  --radius: 4px;
  --shadow: 0 2px 8px 0 rgba(0, 35, 144, 10%);
  --disabled-color: #f7f7f7;
  --disabled-border-color: #e5e5e5;
}

.otaku-switch-container {
  display: flex;
}

.otaku-switch-inner {
  position: relative;
  padding: 0 10px;
}
.otaku-switch-inner .otaku-switch-circle {
  width: 23px;
  height: 23px;
  background-color: white;
  border-radius: 50%;
  position: absolute;
  top: 1px;
  transform: translateX(1px);
  transition: background-color 0.1s, transform 0.35s cubic-bezier(0.45, 1, 0.4, 1);
}
.otaku-switch-inner .otaku-switch-checked {
  transform: translateX(25px);
}
.otaku-switch-inner .otaku-switch-disabled {
  cursor: not-allowed;
}
.otaku-switch-inner .otaku-switch {
  width: 50px;
  height: 25px;
  display: inline-block;
  appearance: none;
  background: #dcdfe6;
  border-radius: 20px;
  transition: background linear 0.2s;
}
.otaku-switch-inner .otaku-switch:checked {
  background: #FF4778;
}
.otaku-switch-inner .otaku-switch:disabled {
  cursor: not-allowed;
}