:root {
  --radius: 4px;
  --shadow: 0 2px 8px 0 rgba(0, 35, 144, 10%);
  --disabled-color: #f7f7f7;
  --disabled-border-color: #e5e5e5;
}

.otaku-checkbox-label {
  display: inline-flex;
  align-items: center;
  color: #595959;
  font-size: 14px;
}
.otaku-checkbox-label .otaku-checkbox {
  width: 16px;
  height: 16px;
  background-color: white;
  border: 1px solid #E5E5E5;
  appearance: none;
  color: #E5E5E5;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 21px;
}
.otaku-checkbox-label .otaku-checkbox:hover {
  border-color: #FF4778;
}
.otaku-checkbox-label .otaku-checkbox:disabled {
  cursor: not-allowed;
  background-color: #F7F7F7;
  border-color: #E5E5E5;
  color: #BFBFBF;
}
.otaku-checkbox-label .otaku-checkbox-label {
  margin-left: 5px;
}
.otaku-checkbox-label .is-half {
  color: #FF4778;
}
.otaku-checkbox-label .is-checked {
  color: #FF4778;
}
.otaku-checkbox-label .otaku-checkbox-disabled {
  color: #BFBFBF;
  cursor: not-allowed;
}