.otaku-drawer-mask {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
}

.otaku-drawer-container {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}
.otaku-drawer-container .otaku-drawer-content {
  background: white;
  overflow: auto;
}
.otaku-drawer-container .otaku-drawer-direction-top {
  position: fixed;
  top: 0;
  left: 0;
}
.otaku-drawer-container .otaku-drawer-direction-left {
  position: fixed;
  top: 0;
  left: 0;
}
.otaku-drawer-container .otaku-drawer-direction-right {
  position: fixed;
  top: 0;
  right: 0;
}
.otaku-drawer-container .transition {
  transition: transform linear 0.5s;
  transform: translateY(0);
}
.otaku-drawer-container .otaku-drawer-direction-bottom {
  position: fixed;
  left: 0;
  bottom: 0;
}