:root {
  --radius: 4px;
  --shadow: 0 2px 8px 0 rgba(0, 35, 144, 10%);
  --disabled-color: #f7f7f7;
  --disabled-border-color: #e5e5e5;
}

.otaku-radio-container {
  display: flex;
  font-size: 14px;
}
.otaku-radio-container .otaku-radio-label {
  display: flex;
  align-items: center;
}
.otaku-radio-container .otaku-radio-label .otaku-radio {
  width: 15px;
  height: 15px;
  border: 1px solid #FF4778;
  border-radius: 50%;
  appearance: none;
  vertical-align: middle;
  display: flex;
  justify-content: center;
  align-items: center;
}
.otaku-radio-container .otaku-radio-label .otaku-radio:checked::before {
  display: inline-block;
  content: "";
  width: 8px;
  height: 8px;
  background: #FF4778;
  border-radius: 50%;
}
.otaku-radio-container .otaku-radio-label .otaku-radio:disabled {
  background: #f7f7f7;
  border-color: #e5e5e5;
}
.otaku-radio-container .otaku-radio-label .otaku-radio:disabled::before {
  display: inline-block;
  content: "";
  width: 8px;
  height: 8px;
  background: #c2c2c2;
  border-radius: 50%;
}
.otaku-radio-container .otaku-radio-label span {
  color: #595959;
  margin-left: 8px;
}
.otaku-radio-container .otaku-radio-label .otaku-radio-disabled {
  color: #e5e5e5;
}