.otaku-timeline {
  width: 100%;
}
.otaku-timeline .otaku-timeline-item {
  width: 100%;
  border-left: 2px solid #e4e7ed;
  padding: 7px;
}
.otaku-timeline .otaku-timeline-item .otaku-timeline-separator {
  width: 15px;
  height: 15px;
  background: #e4e7ed;
  border-radius: 50%;
  margin-left: -15px;
  margin-top: -10px;
}
.otaku-timeline .otaku-timeline-item .otaku-timeline-container {
  padding: 0 10px;
}
.otaku-timeline .otaku-timeline-item .otaku-timeline-title {
  margin: -15px 0 6px;
}
.otaku-timeline .otaku-timeline-item .otaku-timeline-content {
  padding: 5px 0;
}