.otaku-upload-image-preview-container {
  width: 150px;
  height: 150px;
  border: 1.5px dashed #e8e8e8;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.otaku-upload-image-preview-container .otaku-upload-image-preview-mask {
  width: 90%;
  height: 90%;
  background: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 5%;
  left: 5%;
  color: white;
  display: none;
}
.otaku-upload-image-preview-container .otaku-upload-image-preview {
  width: 90%;
  height: 90%;
  text-align: center;
  display: inline-block;
}

.otaku-upload-image-preview-container:hover .otaku-upload-image-preview-mask {
  display: flex;
}