.otaku-cropper-dialog {
  display: grid;
  width: auto;
}
.otaku-cropper-dialog .otaku-dialog-content {
  display: grid;
  grid-template-columns: 700px 300px;
  gap: 20px;
  padding: 20px 20px 0px;
}
.otaku-cropper-dialog .otaku-dialog-content .otaku-image-cropper {
  width: 700px;
  max-height: 500px;
}
.otaku-cropper-dialog .otaku-image-cropper-preview-container {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}
.otaku-cropper-dialog .is-circle {
  border-radius: 50%;
}
.otaku-cropper-dialog .otaku-image-cropper-preview {
  width: 100%;
  height: 200px;
  overflow: hidden;
}
.otaku-cropper-dialog .otaku-cropper-footer {
  display: flex;
  justify-content: flex-end;
  gap: 15px;
}