:root {
  --radius: 4px;
  --shadow: 0 2px 8px 0 rgba(0, 35, 144, 10%);
  --disabled-color: #f7f7f7;
  --disabled-border-color: #e5e5e5;
}

.otaku-button {
  border: none;
  background: white;
  border-radius: var(--radius);
  box-sizing: border-box;
  padding: 0;
  letter-spacing: 2px;
}
.otaku-button:hover {
  cursor: pointer;
}
.otaku-button:disabled {
  cursor: not-allowed;
}
.otaku-button .otaku-button-icon {
  display: inline-block;
  margin: 0 5px;
}

.otaku-button-loading:disabled {
  cursor: pointer;
}
.otaku-button-loading .otaku-icon-loading {
  font-size: 12px;
  display: inline-block;
  animation: loading 1.5s linear 0s infinite forwards;
}

@keyframes loading {
  from {
    transform: rotateZ(0deg);
  }
  to {
    transform: rotateZ(360deg);
  }
}
.otaku-button-size-small {
  padding: 4px 8px;
  height: 24px;
  font-size: 12px;
}

