.otaku-code-container {
  padding: 10px;
  background: white;
  border-radius: 4px;
  position: relative;
  display: grid;
  grid-template-columns: auto 1fr;
}
.otaku-code-container .otaku-code-lang {
  position: absolute;
  right: 10px;
  top: 5px;
  color: #bfbfbf;
}
.otaku-code-container .otaku-code-line-container {
  list-style: none;
  padding: 0 10px;
  border-right: 1px solid #e7e7e7;
  text-align: center;
}
.otaku-code-container .otaku-code-line-container .otaku-code-line {
  list-style: none;
  line-height: 1.45;
  font-size: 14px;
  color: #333333;
}
.otaku-code-container pre {
  background: white;
  margin: 0;
}
.otaku-code-container code {
  font-size: 14px;
  font-family: Consolas, Monaco, monospace;
  line-height: 22px;
}