:root {
  --radius: 4px;
  --shadow: 0 2px 8px 0 rgba(0, 35, 144, 10%);
  --disabled-color: #f7f7f7;
  --disabled-border-color: #e5e5e5;
}

.otaku-pagination {
  display: flex;
}
.otaku-pagination .otaku-pagination-prev-more,
.otaku-pagination .otaku-pagination-next-more,
.otaku-pagination .otaku-pagination-prev,
.otaku-pagination .otaku-pagination-next,
.otaku-pagination .otaku-pagination-item {
  padding: 5px 10px;
  margin-right: 10px;
  background: white;
  color: #262626;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: var(--radius);
  box-shadow: 0 2px 8px 0 rgba(0, 35, 144, 0.1);
  cursor: pointer;
}
.otaku-pagination .is-circle {
  border-radius: 24px;
}
.otaku-pagination .otaku-pagination-item:hover {
  color: #FF4778;
}
.otaku-pagination .otaku-pagination-prev-more,
.otaku-pagination .otaku-pagination-next-more {
  box-shadow: none;
}
.otaku-pagination .otaku-icon-doubleleft,
.otaku-pagination .otaku-icon-doubleright {
  display: none;
}
.otaku-pagination .otaku-pagination-prev .iconfont,
.otaku-pagination .otaku-pagination-next .iconfont {
  font-size: 13px;
}
.otaku-pagination .otaku-pagination-item-acitve {
  color: #FF4778;
}