:root {
  --radius: 4px;
  --shadow: 0 2px 8px 0 rgba(0, 35, 144, 10%);
  --disabled-color: #f7f7f7;
  --disabled-border-color: #e5e5e5;
}

.otaku-input-number-box {
  border: 1px solid #E5E5E5;
  border-radius: var(--radius);
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 88px 32px;
  grid-template-rows: 32px;
  width: fit-content;
}
.otaku-input-number-box:hover {
  border-color: #FF4778;
}
.otaku-input-number-box .otaku-input-number {
  width: 100%;
  height: 100%;
  outline: none;
  border: none;
  padding: 0 10px;
  appearance: none;
}
.otaku-input-number-box .otaku-input-number::-webkit-outer-spin-button, .otaku-input-number-box .otaku-input-number::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
.otaku-input-number-box .otaku-input-number:disabled {
  cursor: not-allowed;
}
.otaku-input-number-box .otaku-input-number-direction {
  background: white;
  border-radius: 0 var(--radius) var(--radius) 0;
  border-left: 1px solid #E5E5E5;
}
.otaku-input-number-box .otaku-input-number-direction li {
  height: 16px;
  font-size: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.otaku-input-number-box .otaku-input-number-direction li:nth-of-type(1) {
  border-bottom: 1px solid #E5E5E5;
}
.otaku-input-number-box .otaku-input-number-direction li .otaku-icon-up,
.otaku-input-number-box .otaku-input-number-direction li .otaku-icon-down {
  font-size: 12px;
  color: #262626;
}
.otaku-input-number-box .otaku-input-number-direction li .input-number-icon-disabled {
  color: #979797;
  cursor: not-allowed;
}

.otaku-input-number-disabled {
  cursor: not-allowed;
  border-color: #E5E5E5;
}
.otaku-input-number-disabled:hover {
  border-color: #e5e5e5;
}

.otaku-input-number-size-small {
  grid-template-rows: 24px;
}
.otaku-input-number-size-small .otaku-input-number-direction li {
  height: 12px;
}

.otaku-input-number-size-middle {
  grid-template-rows: 32px;
}
.otaku-input-number-size-middle .otaku-input-number-direction li {
  height: 16px;
}

.otaku-input-number-size-large {
  grid-template-rows: 40px;
}
.otaku-input-number-size-large .otaku-input-number-direction li {
  height: 20px;
}