:root {
  --radius: 4px;
  --shadow: 0 2px 8px 0 rgba(0, 35, 144, 10%);
  --disabled-color: #f7f7f7;
  --disabled-border-color: #e5e5e5;
}

.otaku-tab {
  display: flex;
  position: relative;
}
.otaku-tab .otaku-tab-pane {
  padding: 5px 10px;
  cursor: pointer;
}
.otaku-tab .otaku-tab-pane-active {
  border-bottom: 2px solid #FF4778;
}

.otaku-tab-active::after {
  content: "";
  width: 100%;
  border-bottom: 1px solid #e5e5e5;
  position: absolute;
  bottom: 0px;
  z-index: 100;
}

.otaku-tab-content {
  margin-top: 16px;
}

.otaku-tab-center {
  display: flex;
  align-items: center;
  justify-content: center;
}