:root {
  --radius: 4px;
  --shadow: 0 2px 8px 0 rgba(0, 35, 144, 10%);
  --disabled-color: #f7f7f7;
  --disabled-border-color: #e5e5e5;
}

.otaku-select-container {
  width: 240px;
}

.otaku-select {
  width: 240px;
  height: 208px;
  box-shadow: var(--shadow);
  background: white;
  border-radius: var(--radius);
  margin-top: 4px;
  overflow: auto;
  margin-top: 5px;
}

.rotate {
  transition: 0.2s transform cubic-bezier(0.075, 0.82, 0.165, 1);
  transform: rotate(180deg);
}

.otaku-select::-webkit-scrollbar {
  width: 5px;
}

.otaku-select::-webkit-scrollbar-thumb {
  background-color: #c8c8c8;
  opacity: 0.25;
  border-radius: 6px;
}

.otaku-select-option {
  height: 40px;
  box-sizing: border-box;
  padding: 0 10px;
  font-size: 14px;
  color: #262626;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}
.otaku-select-option:hover {
  color: #FF4778;
  background: rgba(44, 104, 255, 0.06);
}
.otaku-select-option-active {
  color: #FF4778;
}
.otaku-select-option-multiple-active {
  color: #FF4778;
}