.otaku-tree {
  box-sizing: border-box;
}
.otaku-tree-node {
  height: 35px;
  padding: 3px 0;
  font-size: 14px;
}
.otaku-tree-arrow {
  color: #c8c8c8;
  font-size: 14px;
  transition: transform 0.1s;
}
.otaku-tree-arrow-rotate {
  display: inline-block;
  transform: rotate(90deg);
}
.otaku-tree-loading {
  display: inline-block;
  color: #c8c8c8;
  font-size: 12px;
  animation: rotate 0.8s infinite linear;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}