:root {
  --radius: 4px;
  --shadow: 0 2px 8px 0 rgba(0, 35, 144, 10%);
  --disabled-color: #f7f7f7;
  --disabled-border-color: #e5e5e5;
}

.otaku-input-container {
  display: flex;
  align-items: center;
  width: 240px;
  border: 1px solid #e8e8e8;
  border-radius: var(--radius);
  padding: 8px;
  box-sizing: border-box;
}
.otaku-input-container:hover {
  border-color: #FF4778;
}
.otaku-input-container .otaku-input {
  width: 100%;
  font-size: 13px;
  outline: none;
  border: none;
}
.otaku-input-container .otaku-input-icon-left {
  padding-right: 10px;
}
.otaku-input-container .otaku-input-icon-right {
  padding-left: 10px;
}
.otaku-input-container .otaku-input:disabled {
  cursor: not-allowed;
  background: var(--disabled-color);
}
.otaku-input-container .iconfont {
  font-size: 13px;
  color: #999;
}

.otaku-input-before :first-child {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.otaku-input-before ~ .otaku-input-container {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  margin-right: -1px;
}

.otaku-input-after-border {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  margin-left: -1px;
}

.otaku-input-after :first-child {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.otaku-input-disabled {
  cursor: not-allowed;
  background: var(--disabled-color);
  border: 1px solid var(--disabled-border-color);
}
.otaku-input-disabled:hover {
  border: 1px solid var(--disabled-border-color);
}

.otaku-input-box {
  display: inline-flex;
}

.otaku-input-size-small {
  height: 24px;
}
.otaku-input-size-small .otaku-input {
  font-size: 12px;
}

.otaku-input-size-middle {
  height: 32px;
}
.otaku-input-size-middle .otaku-input {
  font-size: 13px;
}

.otaku-input-size-large {
  height: 40px;
}
.otaku-input-size-large .otaku-input {
  font-size: 16px;
}

.otaku-textarea-container {
  display: flex;
  flex: 1;
  align-items: center;
  border: 1px solid #e8e8e8;
  border-radius: var(--radius);
}
.otaku-textarea-container:hover {
  border-color: #FF4778;
}
.otaku-textarea-container .otaku-input-textarea {
  width: 100%;
  outline: none;
  border: none;
  padding: 8px;
}